						description:`A través de esta jornada espiritual usted entenderá la cultura de los Incas y el legado arqueológico que dejaron atrás.
Desafíese a usted mismo caminando esta ruta sagrada hacia Machu Picchu tal y como muchos antiguos Incas hicieron hace cientos de años.
Esta excursión guiada lo llevará a través de los cerros y valles de la renombrada cordillera de los Andes. Caminará por viejas ruinas y ciudades durante la jornada y disfrutará de la diversa flora y fauna, única de esta región montañosa. Un guía experto le contará historias de la gente Inca mientras lo mantiene informado acerca de las montañas, animales y plantas que verá en el camino.
A pesar de que hay un cocinero y comidas durante el camino, le recomendamos que traiga botanas para mantener una alto nivel energético. Todas las tiendas de campaña y bolsas de dormir están incluidas en el costo y habrá personas encargadas de llevar el equipaje.
Hay al menos un guía por cada 10 participantes, estos llevan el paso dependiendo del estado físico del grupo. Los grupos también pueden dividirse en grupos más pequeños en caso de que los participantes caminen a un paso distinto. Hay un maletero por cada 2 ó 3 personas, estos cargarán todo el equipaje para el campamento. Los maleteros no cargarán mochilas u objetos personales a menos que el cliente indique que desea un maletero adicional.`,
